import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, GrupoHistorico } from '../../../../models/dtos/ger/grupoHistorico';

interface GrupoHistoricoEditViewProps extends EditViewProps<GrupoHistorico> {}

const GrupoHistoricoEditView: FC<GrupoHistoricoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<GrupoHistorico>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção de Grupos de Histórico">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 6) } }}
              id="codigo"
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
              isPrependLabel={true}
              label="Código"
              placeholder="Informe o Código do Grupo."
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <InputGroupInline
          formProps={{ ...formProps, validation: { required: true, maxLength: 120 } }}
          id="descricao"
          isPrependLabel={true}
          label="Descrição"
          placeholder="Informe a Descrição do Grupo."
        />
      </EditForm>
    </WindowComponent>
  );
};

export default GrupoHistoricoEditView;
